import { DeleteOutlined, EditOutlined, ShopOutlined, TeamOutlined } from '@ant-design/icons'
import { Button, Card, Checkbox, Col, DatePicker, Form, Input, InputNumber, Modal, Popconfirm, Row, Select, Space, Table, Tag, Tooltip } from 'antd'
import TextArea from 'antd/lib/input/TextArea';
import React, { useEffect, useState } from 'react'
import { useStoreActions, useStoreState } from '../../../store/hooks/easyPeasy'
import { SelectPartner } from '../../select/SelectIPartner'
import GetColumnSearchProps from '../../utils/getColumnSearchProps';
const { Option } = Select;


export default function IspBillListSDL(props) {

    const check = useStoreState(state => state.isp.check);
    const ispClientBillInfoList = useStoreState(state => state.isp.ispClientBillInfoList);
    const fetchispClientBillInfoList = useStoreActions(state => state.isp.fetchispClientBillInfoList);
    const sendSmsUnpaidIspClient = useStoreActions(state => state.isp.sendSmsUnpaidIspClient);


    const [form] = Form.useForm();
    const [formUpdate] = Form.useForm();

    const columns1 = [

        {
            title: 'Client Id',
            dataIndex: 'clientCustomId',
            key: 'clientCustomId',
            ...GetColumnSearchProps('clientCustomId', 'Client Id'),

        },
        {
            title: 'Client Name',
            dataIndex: 'clientName',
            key: 'clientName',
            ...GetColumnSearchProps('clientName', 'Client Name'),

        },
        {
            title: 'Mobile No',
            dataIndex: 'clientMobile',
            key: 'clientMobile',
            ...GetColumnSearchProps('clientMobile', 'Client Mobile'),

        },
        {
            title: 'Address',
            dataIndex: 'mailingAddress',
            key: 'mailingAddress',

        },
        {
            title: 'Package Name',
            dataIndex: 'packageName',
            key: 'packageName',

        },
        {
            title: 'Package Price',
            dataIndex: 'billAmount',
            key: 'billAmount',

        },
        {
            title: 'Payment Date',
            render: (text: any, record: any, index) => (
                <Space size="middle">
                    {record?.billPaymentDate === "null" ? 'N/A' : record?.billPaymentDate}

                </Space>
            ),
        },       
        


    ]
    const columns2 = [

        {
            title: 'Client Id',
            dataIndex: 'clientCustomId',
            key: 'clientCustomId',
            ...GetColumnSearchProps('clientCustomId', 'Client Id'),

        },
        {
            title: 'Client Name',
            dataIndex: 'clientName',
            key: 'clientName',
            ...GetColumnSearchProps('clientName', 'Client Name'),

        },
        {
            title: 'Mobile No',
            dataIndex: 'clientMobile',
            key: 'clientMobile',
            ...GetColumnSearchProps('clientMobile', 'Client Mobile'),

        },
        {
            title: 'Address',
            dataIndex: 'mailingAddress',
            key: 'mailingAddress',

        },
        {
            title: 'Package Name',
            dataIndex: 'packageName',
            key: 'packageName',

        },
        {
            title: 'Package Price',
            dataIndex: 'billAmount',
            key: 'billAmount',

        },     
        
        {
            title: 'Payable Date',
            render: (text: any, record: any, index) => (
                <Space size="middle">
                    {record?.billPayableDate === "null" ? 'N/A' : record?.billPayableDate}

                </Space>
            ),
        }


    ]
    const [colData,setcolData] =useState<any>([])

    const onFinish = (values: any) => {
        if (values.billStatus==1){
            setcolData(columns1)
        } 

        if (values.billStatus==0){
            setcolData(columns2)
        }

        let postData: any = {
            billStatus: values.billStatus,
            partnerId: values.partnerId.value
        }
        fetchispClientBillInfoList(postData)
    };


    useEffect(() => {
        setPageTitle();
    }, []);




    const setPageTitle = () => {
        var pageTitleEle = document.getElementById('pageTitle');
        var pageTitle = 'ISP'
        return pageTitleEle ? pageTitleEle.innerHTML = pageTitle : ''
    }

    const [row, setRow] = useState<any>();



    const [partnerId, setPartnerId] = useState<any>(null);

    const onchnagePartner = (val) => {
        setPartnerId(val)
    }

    const [selectedRowKeys, setselectedRowKeys] = useState<any>([]);
    const [selectedValue, setselectedValue] = useState<any>([]);

    const onSelectChange = (selectedRowKeys, value) => {
        setselectedRowKeys(selectedRowKeys);
        setselectedValue(value);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };


    const sendSms = () => {

        let data = selectedValue?.map((item, key) => {
            return {
                billAmount: item.billAmount,
                billGenerateDate: item.billGenerateDate,
                billId: item.billId,
                billPayableDate: item.billPayableDate,
                billingMonth: item.billingMonth,
                billingYear: item.billingYear,
                clientCustomId: item.clientCustomId,
                clientId: item.clientId,
                clientMobile: item.clientMobile,
                clientName: item.clientName,
                mailingAddress: item.mailingAddress,
                packageName: item.packageName,

            }
        })
        sendSmsUnpaidIspClient({
            "ispClientBillInfoResponseList": data,
            "paymentStatus": 0
        })
        setselectedRowKeys([]);
        setselectedValue([]);
    }


    return (
        <>
            <Form layout="vertical" onFinish={onFinish} id='create-class' form={form} >
                <Row gutter={15} className="box-shadow p-15">
                    <Col xs={24} sm={24} md={24} lg={4} xl={4}></Col>
                    <Col xs={24} sm={24} md={24} lg={6} xl={6}>

                        <Form.Item
                            name="billStatus"
                            label="Bill Status:"
                            className="title-Text"
                            rules={[
                                { required: true, message: "Please select bill status" },
                            ]}
                        >
                            <Select placeholder="Select Bill Status" style={{ width: "100%" }} allowClear>
                                <Option value="1">Paid</Option>
                                <Option value="0">Unpaid</Option>
                            </Select>


                        </Form.Item>

                    </Col>
                    <Col xs={24} sm={24} md={24} lg={6} xl={6}>

                        <Form.Item
                            name="partnerId"
                            label="Partner:"
                            className="title-Text"
                            rules={[
                                { required: true, message: "Please select bill status" },
                            ]}
                        >
                            <SelectPartner onChange={(val) => onchnagePartner(val)} selected={partnerId} name="PIP" level={2} />


                        </Form.Item>

                    </Col>

                    <Col xs={24} sm={24} md={24} lg={4} xl={4}>

                        <Space size="small" style={{ marginTop: 30 }} >
                            <Button type="primary" htmlType="submit" >
                                Search
                            </Button>
                        </Space>

                    </Col>

                </Row>
            </Form>
            {ispClientBillInfoList?.length > 0 &&
                <Card title="ISP Client List" >

                    <Table rowSelection={rowSelection} bordered={true} dataSource={ispClientBillInfoList} columns={colData} />

                    <Button type="primary" style={{ float: "right" }} onClick={() => sendSms()}>Send SMS</Button>
                </Card>

            }

        </>
    )
}
